@import "normalizez.scss";
@import "resets.scss";
* {
    box-sizing: border-box; }

$color-blue: #5290EA;
$color-orange: #F28B64;

@font-face {
    font-family: "MontserratT";
    src: url('../font/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }
@font-face {
    font-family: "MontserratT";
    src: url('../font/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }
html {
    font-family: "MontserratT";
    font-weight: regular; }

.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }
.main-section {
    background: #F0F0F0;
    height: 800px;
    padding-top: 16px;
    position: relative;
    overflow: hidden;
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center;
        font-weight: bold; }
    &__logo {
        height: 69px; }
    &__first-ellipse {
        position: absolute;
        top: 129px;
        right: 80px; }
    &__second-ellipse {
        position: absolute;
        right: -120px;
        top: 650px; } }
.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }

    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        &:last-child {
            margin-right: 0px; } } }
.left-wrapper {
    position: relative;
    img {
        width: 883px;
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        width: 565.95px;
        height: 220px;
        top: 159px;
        left: -30px;
        background: $color-blue;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        position: absolute;
        z-index: 2; }
    &__rectangle-orange {
        width: 618.43px;
        height: 220px;
        left: -30px;
        top: 160px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-50.81deg);
        position: absolute;
        z-index: 3; }
    &__rectangle-grey {
        width: 608.97px;
        height: 220px;
        left: 10px;
        top: 190px;
        background: #E2E2E2;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-36.15deg);
        position: absolute;
        z-index: 1; } }
.right-wrapper {
    width: 372px;
    position: absolute;
    top: 150px;
    right: 76px;
    z-index: 5;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__title {
        margin-bottom: 5px; }
    &__text {
        font-size: 16px;
        margin-bottom: 20px; } }

.title {
    font-size: 72px;
    position: relative;
    font-weight: bold;
    &__hr {
        margin: 0;
        border: none;
        border-top: 1px solid black;
        width: 65px;
        bottom: 0px;
        position: ablosute; }
    &_ftr-title {
        font-size: 60px; } }
.icons {
    display: inline-flex;
    &__item {
        border: 1px solid $color-blue;
        width: 40px;
        height: 40px;
        border-radius: 100px;
        margin-right: 17px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:last-child {
            margin-right: 0; }
        i {
            font-size: 21px; } }
    font-size: 21px; }
.buttons {
    display: inline-flex;

    &__icon-btn {
        display: inline-flex;
        align-items: center; }
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        color: white;
        font-size: 18px;
        font-weight: bold; }
    img {
        margin-right: 12px;
        &__text {
            font-size: 16px; } } }

.ellipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.4) 14.85%, rgba(72, 138, 234, 0.4) 53.25%, rgba(44, 44, 44, 0.4) 85.74%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 150px;

    &_big {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129.33px;
        height: 129.33px;
        background: #F0F0F0;
        border-radius: 150px;
        &_white {
            background: white;
            width: 129.33px;
            height: 129.33px;
            border-radius: 150px; }
        &_big {
            width: 188px;
            height: 188px; } } }

// Вторая секция
.second-section {
